import store from '@/store';
// API
import { getRelatedDeviceTypeAction, selectGroup, selectDeviceByGroupId, getRepairUser } from '@/api/equipment';
// 文字常量
import { STATUS_CODE } from '@/utils/constantsFile';
const CommonService = {
  /**
   * @description: 获取人员列表
   * @return: void
  */
  getUsers: function() {
    getRepairUser().then(res => {
      if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
        var result = {
          name: 'userData',
          data: res.rows
        };
        store.commit('setFiltersData', result);
      }
    });
  },
  /**
   * @description: 查询车间信息
   * @return: void
  */
  getGroup: function() {
    selectGroup()
      .then(res => {
        if (res.code === '0000') {
          var result = {
            name: 'group',
            data: res.rows
          };
          store.commit('setFiltersData', result);
        }
      });
  },
  /**
   * @description: 查询设备类型
   * @return: void
  */
  getDeviceName: function(id) {
    selectDeviceByGroupId({
      groupId: id
    })
      .then(res => {
        if (res.code === '0000') {
          var result = {
            name: 'RelatedDeviceName',
            data: res.rows
          };
          store.commit('setFiltersData', result);
        }
      });
  },
  // // 查询维修类型
  // getRepairType: function() {
  //   var result = {
  //     name: 'repairTypeData',
  //     data: repairTypeData.rows.data
  //   };
  //   store.commit('setFiltersData', result);
  //   return result;
  // },
  // 查询设备类型
  getRelatedDeviceType: function() {
    getRelatedDeviceTypeAction()
      .then(res => {
        if (res.code === '0000') {
          var result = {
            name: 'RelatedDeviceType',
            data: res.rows
          };
          store.commit('setFiltersData', result);
        }
      });
  },
  // 生成复杂搜索的条件
  generateSearchCondition(conditionArr1, groupOperator, conditionArr2) {
    var searchData = [];
    if (conditionArr1 !== undefined) {
      searchData.conditionArr1 = JSON.parse(
        JSON.stringify(conditionArr1)
      );
      for (var i in searchData.conditionArr1) {
        searchData.conditionArr1[
          i
        ].value = searchData.conditionArr1[i].value.toString();
      }
    }
    if (groupOperator !== undefined) {
      searchData.groupOperator = groupOperator;
    }
    if (conditionArr2 !== undefined) {
      searchData.conditionArr2 = JSON.parse(
        JSON.stringify(conditionArr2)
      );
      for (var x in searchData.conditionArr2) {
        searchData.conditionArr2[
          x
        ].value = searchData.conditionArr2[x].value.toString();
      }
    }
    return searchData;
  },
  // 生成复杂搜索的条件JSON
  generateSearchConditionJSON(conditionJson1, operator, conditionJson2) {
    var searchData = {};
    if (conditionJson1 !== undefined) {
      searchData.conditionJson1 = JSON.parse(
        JSON.stringify(conditionJson1)
      );
      for (var i in searchData.conditionJson1) {
        searchData.conditionJson1[
          i
        ].value = searchData.conditionJson1[i].value.toString();
      }
    }
    if (operator !== undefined) {
      searchData.operator = operator;
    }
    if (conditionJson2 !== undefined) {
      searchData.conditionJson2 = JSON.parse(
        JSON.stringify(conditionJson2)
      );
      for (var x in searchData.conditionJson2) {
        searchData.conditionJson2[
          x
        ].value = searchData.conditionJson2[x].value.toString();
      }
    }
    return searchData;
  },
  // 其他方法
  typeOf(obj) {
    const toString = Object.prototype.toString;
    const map = {
      '[object Boolean]': 'boolean',
      '[object Number]': 'number',
      '[object String]': 'string',
      '[object Function]': 'function',
      '[object Array]': 'array',
      '[object Date]': 'date',
      '[object RegExp]': 'regExp',
      '[object Undefined]': 'undefined',
      '[object Null]': 'null',
      '[object Object]': 'object'
    };
    return map[toString.call(obj)];
  },
  // 只解决date，reg类型，其他的可以自己添加
  deepCopy(data) {
    const t = this.typeOf(data);
    var o;
    if (t === 'array') {
      o = [];
    } else if (t === 'object') {
      o = {};
    } else {
      return data;
    }

    if (t === 'array') {
      for (let i = 0; i < data.length; i++) {
        o.push(this.deepCopy(data[i]));
      }
    } else if (t === 'object') {
      for (var i in data) {
        o[i] = this.deepCopy(data[i]);
      }
    }
    return o;
  }
};

export { CommonService };
