import { postAplicationJSON } from '@/utils/request';

// ********************设备保养********************
// 工位机根据当前用户查询和设备用户关联的设备未保养完成的保养任务
export const getPageMaintainTaskByDeviceCurrentUser = (param, jsonData) => postAplicationJSON('maintainTask/getPageMaintainTaskByCurrentUser.action?', param, jsonData);

// 获取分组树形结构
export const getDeviceTree = (param, jsonData) => postAplicationJSON('deviceGroup/getTree.action?', param, jsonData);
// 询用户下的设备保养树形结构
export const getTreeByMaintainUser = (param, jsonData) => postAplicationJSON('deviceGroup/getTreeByMaintainUser.action?', param, jsonData);
// 根据设备Code查询该设备未完成的保养记录
export const getPageToBeMaintainedMaintainTaskByDeviceCode = (param, jsonData) => postAplicationJSON('maintainTask/getPageToBeMaintainedMaintainTaskByDeviceCode.action?', param, jsonData);
// 工位机根据当前用户查询用户关联的设备未保养完成的保养任务
export const getPageMaintainTaskByCurrentUser = (param, jsonData) => postAplicationJSON('maintainTask/getPageMaintainTaskByCurrentUser.action?', param, jsonData);

// ********************维修任务********************
// 查询维修中列表_报工台
export const getPageRepairTaskStation = (param, jsonData) => postAplicationJSON('repairTask/getPageRepairTaskStation.action?', param, jsonData);
